import { Component, forwardRef, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'mbaas-swiss-input',
  templateUrl: './swiss-input.component.html',
  styleUrls: ['./swiss-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SwissInputComponent),
      multi: true
    }
  ]
})
export class SwissInputComponent implements ControlValueAccessor {

  @ViewChild('Mask') input;
  @ViewChild('fake') inputFake;
  mostrar: boolean;

  @Input() preffix: string;
  @Input() suffix: string;
  // tslint:disable-next-line: no-inferrable-types
  @Input() maxLength: number = Infinity;
  @Input() maskValue: (value: string) => string = (value: string) => value;
  @Input() mapValue: (value: string) => string = (value: string) => value;
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() change = new EventEmitter();
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() focus = new EventEmitter();
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() focusout = new EventEmitter();
  // tslint:disable-next-line: no-inferrable-types member-ordering
  @Input() inputClass: string = 'formGroup__input';
  // tslint:disable-next-line: no-inferrable-types member-ordering
  @Input() placeHolder: string = '';
  // tslint:disable-next-line: no-inferrable-types member-ordering
  @Input() restric: string = '';
  // tslint:disable-next-line: no-inferrable-types member-ordering
  @Input() password: boolean = false;
  // tslint:disable-next-line: no-inferrable-types member-ordering
  disabled: boolean = false;

  // tslint:disable-next-line: no-input-rename variable-name member-ordering
  @Input('value') _value: string;
  // tslint:disable-next-line: member-ordering
  @Input() error: boolean;
  onChange: any = () => { };
  onTouched: any = () => { };

  constructor() {}

  touch(): void {
    this.onTouched();
  }

  get value() {
    return this._value;
  }

  set value(value) {
    this._value = this.mapValue(this.filtro(value));
    this.onChange(this._value);
    this.onTouched();
    this.change.emit(this._value);
  }

  writeValue(value) {
    this._value = this.mapValue(this.filtro(value));
    this.change.emit(this._value);
    setTimeout(() => {
      if ( this.input ) {
        this.input.nativeElement.value = this.value;
      }
      this.mostrar = !!!this.error;
    }, 10);
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  sendFocus(): void {
    this.onTouched();
    this.focus.emit();
  }

  sendFocusOut(): void {
    this.focusout.emit();
  }

  prevent(textInput): void {
    this.value = textInput.target.value;
    textInput.target.value = this.value;
  }

  filtro(valor: string): string {
    const regex = this.restric === '' ? '.*' : `[${this.restric}]*`;
    valor = valor.match(new RegExp(regex, 'g')).reduce( (a, b) => a + b, '');
    if (
      valor.length > this.maxLength
    ) {
      return valor.substring(0, this.maxLength);
    }
    return valor;
  }

  mostrarInput(): void {
    this.mostrar = false;
    setTimeout(() => {
      this.input.nativeElement.value = this._value;
      this.input.nativeElement.focus();
    }, 10);
  }
}
